<template>
  <div class="header">
    <div class="logo">
      <a class="logo-img__link" href="#"
        ><img class="logo__img" src="../assets/logo-wether.svg" alt="logo"
      /></a>
      <a class="logo__desc" href="#">VUE WEATHER</a>
    </div>
    <div class="header__select">
      <input
        placeholder="Look for the city"
        :cityName="cityName"
        v-model="cityName"
        @change="getCityName"
        type="text"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cityName: "",
      cityNameUpper: '',
      temper: "",
      nowDay: new Date().toLocaleDateString(),
      nowTime: new Date().toLocaleTimeString().slice(0, -3),
      nowPressure: "",
    };
  },
  methods: {
    getCityName() {
      this.axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${this.cityName}&appid=9f9111c452e0ffd9482e1f7b42a9afba`
        )
        .then((response) => {
          this.temper = Math.round(response.data.main.temp - 273);
          this.nowPressure = response.data.main.pressure;
          this.cityNameUpper = response.data.name;
          console.log(response.data);
          this.$emit('login', {
            cityNameUpper: this.cityNameUpper,
            temper: this.temper,
            nowDay: this.nowDay,
            nowTime: this.nowTime,
            nowPressure: this.nowPressure,
          })
        });
    },
  },
};
</script>

<style lang="sass" scoped>
.header
  display: flex
  justify-content: space-between
  padding-top: 30px
  align-items: center

  .logo
    display: flex
    align-items: center
    gap: 20px

  .logo__img
    height: 50px
    width: 50px

  .logo__desc
    color: white

  input
    border: none
    outline: none
    padding: 10px

  .nows
    color: white
</style>
